.coin-container{
    display: flex;
    justify-content: center;
}
.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;

}
.coin h1{
    font-size: 16px;
    width: 150px;
}
.coin-symbol{
    text-transform: uppercase;
}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}
.coin-price{
    width: 110px;
}
.coin-volume{
    width: 155px;
}
.coin-marketcap{
    width: 230px;
    text-align: center; 
}
.coin-percent{
    width: 100%;
    margin-left: 60px;
}
.reds{
    color: #f00;
}
.greens{
    color:#11d811;
}